  box-sizing: border-box;
  color: #fff;
  z-index: 3;
}

.navigation__bar_type_scroll {
  background: rgba(47, 113, 229, 0.7);
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
}

.navigation__links-wrapper {
  min-height: 48px;
