.popup {
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  /* Location of the box */
  top: 0;
  /* Location of the box */
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.5);
  /* Black w/ opacity */
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
  transition: all linear 0.3s;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

.popup_opened {
  visibility: visible;
  pointer-events: all;
  opacity: 1;
  transition-delay: 0s;
}

.popup__container {
  background-color: #fff;
  width: 100%;
  max-width: 430px;
  border-radius: 10px;
  position: relative;
}

.close-button {
  width: 100%;
  max-width: 40px;
  min-height: 40px;
  margin: auto;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: -40px;
  right: -40px;
  z-index: 1;
  transition: opacity 0.2s linear;
  padding: 0;
  background-image: url(/stage_react_api/static/media/close-button.0cba6a4c.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.close-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

