.card {
  width: 100%;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
  max-width: 400px;
  transition: all 0.2s linear;
}

.card:hover {
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px,
    rgba(17, 17, 26, 0.05) 0px 8px 32px;
  -webkit-transform: translate(0, -5px);
          transform: translate(0, -5px);
}

.card__favourite {
  max-width: 40px;
  min-height: 40px;
  width: 100%;
  position: absolute;
  border: none;
  background-color: #fff;
  top: 18px;
  right: 15px;
  border-radius: 8px;
  background-image: url(/stage_react_api/static/media/bookmark-gray.5e81c92b.svg);
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
  transition: all linear 0.3s;
}

.card__favourite:hover {
  background-image: url(/stage_react_api/static/media/bookmark-black.75c29182.svg);
}

.card__favourite_active {
  background-image: url(/stage_react_api/static/media/bookmark-blue.05f1325d.svg);
}

.card__favourite_active:hover {
  background-image: url(/stage_react_api/static/media/bookmark-blue.05f1325d.svg);
}

.card__delete {
  max-width: 40px;
  min-height: 40px;
  width: 100%;
  position: absolute;
  border: none;
  background-color: #fff;
  top: 18px;
  right: 15px;
  border-radius: 8px;
  background-image: url(/stage_react_api/static/media/trash-gray.5c12acc2.svg);
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
  transition: all linear 0.3s;
}

.card__delete:hover {
  background-image: url(/stage_react_api/static/media/trash-black.d8e9b6df.svg);
